<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <div class="d-flex" style="width: 100%;">
        <v-icon large>mdi-cards</v-icon>
        <div class="text-h4">Aw Shucks!</div>
        <div style="margin-left: auto">
          <v-icon @click="playAgain()"  medium>mdi-refresh</v-icon>
        </div>
      </div>

    </v-app-bar>

    <v-main >
      <BeginGame v-if="showSetup" @start-game="startGame" />
      <div v-if="!showSetup && !gameOver">
        <RoundHeader :numCards="game.currentCardRound" :dealer="game.dealer" :bidder="game.bidder" />
        <BetInput v-if="inputBets" :totScores="game.scores" :maxCards="game.currentCardRound" :players="game.roundOrder" :bidder="game.bidder" @start-round="startRound" />
        <BetResults v-if="!inputBets" :totScores="game.scores" :maxCards="game.currentCardRound" :players="game.roundOrder" :bidder="game.bidder" :bets="game.roundBets" @start-next-round="startNextRound" :isLastRound="isItLastRound()"/>
      </div>
      <div v-if="gameOver">
        <GameOverScreen :players="game.roundOrder" :scores="game.scores" @play-again="playAgain" />
      </div>
    </v-main>
  </v-app>
</template>

<script>
import BeginGame from './components/BeginGame';
import RoundHeader from './components/RoundHeader';
import BetInput from './components/BetInput';
import BetResults from './components/BetResults';
import GameOverScreen from './components/GameOverScreen';

export default {
  name: 'App',

  components: {
    BeginGame,
    RoundHeader,
    BetInput,
    BetResults,
    GameOverScreen
  },

  data: () => ({
    showSetup: true,
    inputBets: true,
    gameOver: false,
    game: {
      dealer: "",
      bidder: "",
      totNumCards: 0,
      currentCardRound: 0,
      players: [],
      scores: [],
      roundOrder: [],
      roundOrderIdxs: [],
      roundBets: [],
      isDecreasingCards: true
   }
    //
  }),
  methods: {
    startGame (numCards, players, dealer) {
      this.game.totNumCards = numCards;
      this.game.currentCardRound = this.game.totNumCards;
      this.game.players = players;
      this.game.dealer = dealer;
      this.setBidder();
      this.game.scores = Array(this.game.players.length).fill(0);
      this.game.isDecreasingCards = true;
      this.showSetup = false;

      this.game.roundOrder = this.getRoundOrder();
      this.game.scores = this.getScoreOrder();
    },
    startRound (bets) {
      this.inputBets = false;
      this.game.roundBets = bets;
    },
    startNextRound (scores) {
      for(var i = 0; i < this.game.players.length; i++) {
        let idx = this.game.roundOrderIdxs[i];
        this.game.scores[idx] = scores[i];
      }
      this.inputBets = true;
      this.nextRound();
    },
    getOrderIdxs() {
      let player = this.game.players.find(o => o.name === this.game.bidder); 
      let playeridx = player.num - 1;
      this.game.roundOrderIdxs = [];
      for(var i = playeridx; i < this.game.players.length; i++) {
        this.game.roundOrderIdxs.push(i);
      }
      for(i = 0; i < playeridx; i++) {
        this.game.roundOrderIdxs.push(i);
      } 
    },
    getRoundOrder () {
      this.getOrderIdxs();

      return this.game.roundOrderIdxs.map(i => this.game.players[i].name)
    },
    getScoreOrder () {
      this.getOrderIdxs();

      return this.game.roundOrderIdxs.map(i => this.game.scores[i]);
    },
    setDealer() {
      let player = this.game.players.find(o => o.name === this.game.dealer);
      if(player.num != this.game.players.length) {
        this.game.dealer = this.game.players.find(o => o.num == player.num + 1).name
      }
      else {
        this.game.dealer = this.game.players.find(o => o.num == 1).name
      }
    },
    setBidder () {
      let player = this.game.players.find(o => o.name === this.game.dealer);
      if(player.num != this.game.players.length) {
        this.game.bidder = this.game.players.find(o => o.num == player.num + 1).name
      }
      else {
        this.game.bidder = this.game.players.find(o => o.num == 1).name
      }
    },
    nextRound () {
      if(this.game.isDecreasingCards && this.game.currentCardRound > 1) {
        this.game.currentCardRound--;
      }
      else if(!this.game.isDecreasingCards && this.game.currentCardRound != this.game.totNumCards) {
        this.game.currentCardRound++;
      }
      else if(this.game.isDecreasingCards && this.game.currentCardRound == 1) {
        this.game.isDecreasingCards = false;
      }
      else if(!this.game.isDecreasingCards && this.game.currentCardRound == this.game.totNumCards) {
        this.endGame();
      }

      this.setDealer();
      this.setBidder();
      this.game.roundOrder = this.getRoundOrder();
      this.game.scores = this.getScoreOrder();
    },
    isItLastRound() {
      console.log("here");
      if(!this.game.isDecreasingCards && this.game.currentCardRound == this.game.totNumCards) {
        return true;
      }
      return false;
    },
    endGame() {
      console.log('Game Over');
      this.inputBets = false;
      this.gameOver = true;
    },
    playAgain() {
      if (!confirm('Are you sure you want to play again?')) {
        return;
      } 
      this.gameOver = false;
      this.showSetup = true;
    }
  }
};
</script>
