<template>
  <v-container>
    <v-card
      class="mx-auto"
      outlined
      >
      <v-card-title class="justify-center">Bid for {{players[currentidx]}}</v-card-title> 
      <v-card-text class="text-center"> Current card total is {{getSum()}} out of {{maxCards}}</v-card-text>
      <v-flex class="justify-center d-flex flex-wrap">
        <v-btn 
          v-for="(num, i) in Array(maxCards+1).keys()" 
          :key="i" 
          :class="{'primary': selectedBids[currentidx] == i}"
          :disabled="optionUnavalible(i)"
          class="ma-2 white--text"
          @click="changeSelection(i)"
          outlined
          >{{num}}</v-btn>
      </v-flex>
      <v-flex class="justify-center d-flex flex-wrap">
        <v-btn
          rounded
          color="secondary"
          class="ma-2"
          :disabled="currentidx == 0"
          @click="prevPlayer"
          dark
          >
            prev 
        </v-btn>
        <v-btn
          rounded
          color="secondary"
          class="ma-2"
          v-if="currentidx != players.length - 1"
          :disabled="selectedBids[currentidx] == -1"
          @click="nextPlayer"
          dark
          >
            next 
        </v-btn>
         <v-btn
          rounded
          color="green"
          class="ma-2"
          v-if="currentidx == players.length - 1"
          :disabled="!canStart"
          @click="startRound"
          dark
          >
            start round
        </v-btn>
      </v-flex>
    </v-card>
    <RoundScores :totScores="totScores" :players="players" :bidder="bidder" :bets="selectedBids" :scores="selectedActuals"/>
  </v-container>
</template>

<script>
import RoundScores from './RoundScores';

export default {
  name: "BetInput",
  components: {
    RoundScores
  },
  props: {
    players: Array,
    bidder: String,
    maxCards: Number,
    totScores: Array
  },
  data: () => ({
    currentidx: 0,
    selectedBids: [],
    selectedActuals: []
  }),
  created () {
    this.selectedBids = Array(this.players.length).fill(-1);
    this.selectedActuals = Array(this.players.length).fill(-1);
  },
  methods: {
    nextPlayer() {
      if(this.currentidx < this.players.length) {
        this.currentidx++;
      }
    },
    prevPlayer() {
      if(this.currentidx > 0) {
        this.currentidx--;
      }
    },
    changeSelection(selection) {
      // need this for array reactivty to update
      this.$set(this.selectedBids, this.currentidx, selection);
    },
    getSum(subVal=0) {
      let sum = 0;
      for(var i = 0; i < this.players.length - subVal; i++) {
        sum += this.selectedBids[i] == -1 ? 0 : this.selectedBids[i];
      }
      return sum
    },
    optionUnavalible(buttonVal) {
      let sum = this.getSum(1);

      if(this.currentidx == this.players.length - 1 && sum + buttonVal == this.maxCards) {
        return true;
      }

      return false;
    },
    startRound() {
      this.$emit("start-round", this.selectedBids);
    } 
  },
  computed: {
    canStart() {
      for(var i = 0; i < this.players.length; i++) {
        if(this.selectedBids[i] == -1) {
          return false;
        }
      }
      let sum = this.getSum();
      if(sum == this.maxCards) {
        return false;
      }

      return true;
    }
  }
}
</script>