<template>
  <v-container>
    <v-card
      class="mx-auto"
      outlined
      >
      <v-card-title class="justify-center">Results for {{players[currentidx]}}</v-card-title> 
      <v-card-text class="text-center"> Current card total is {{getSum(-1)}} out of {{maxCards}}</v-card-text>
      <v-flex class="justify-center d-flex flex-wrap">
        <v-btn 
          v-for="(num, i) in Array(maxCards+1).keys()" 
          :key="i" 
          :class="{'green': selectedActuals[currentidx] == i && selectedActuals[currentidx] == bets[currentidx],
                     'red': selectedActuals[currentidx] == i && selectedActuals[currentidx] != bets[currentidx]}"
          :color="bets[currentidx] == i && selectedActuals[currentidx] != i ? 'blue' : ''"
          :disabled="optionUnavalible(i)"
          class="ma-2 white--text"
          @click="changeSelection(i)"
          outlined
          >{{num}}</v-btn>
      </v-flex>
      <v-flex class="justify-center d-flex flex-wrap">
        <v-btn
          rounded
          color="secondary"
          class="ma-2"
          :disabled="currentidx == 0"
          @click="prevPlayer"
          dark
          >
            prev 
        </v-btn>
        <v-btn
          rounded
          color="secondary"
          class="ma-2"
          v-if="currentidx != players.length - 1"
          :disabled="selectedActuals[currentidx] == -1"
          @click="nextPlayer"
          dark
          >
            next 
        </v-btn>
         <v-btn
          rounded
          color="green"
          class="ma-2"
          v-if="currentidx == players.length - 1"
          :disabled="!canDoNextRound"
          @click="startNextRound"
          dark
          >
            {{!isLastRound ? 'next round' : 'end game'}}
        </v-btn>
      </v-flex>
    </v-card>
    <RoundScores :totScores="newTotScores" :players="players" :bidder="bidder" :bets="bets" :scores="scores" :isScoring="true"/>
  </v-container>
</template>

<script>
import RoundScores from './RoundScores';

export default {
  name: "BetResults",
  components: {
    RoundScores
  },
  props: {
    players: Array,
    bidder: String,
    maxCards: Number,
    bets: Array,
    totScores: Array,
    isLastRound: Boolean
  },
  data: () => ({
    currentidx: 0,
    selectedActuals: [],
    scores: [],
    newTotScores: []
  }),
  created () {
    this.selectedActuals = Array(this.players.length).fill(-1);
    this.scores = [...this.selectedActuals];
    this.newTotScores = [...this.totScores];
  },
  methods: {
    nextPlayer() {
      if(this.currentidx < this.players.length) {
        this.currentidx++;
      }
    },
    prevPlayer() {
      if(this.currentidx > 0) {
        this.currentidx--;
      }
    },
    changeSelection(selection) {
      // need this for array reactivty to update
      let score = selection;
      if(selection == this.bets[this.currentidx]) {
        score += 10;
      }

      this.$set(this.selectedActuals, this.currentidx, selection);
      this.$set(this.scores, this.currentidx, score);

      let newScore = this.totScores[this.currentidx] + score;
      this.$set(this.newTotScores, this.currentidx, newScore);
    },
    getSum(subVal) {
      let sum = 0;
      for(var i = 0; i < this.players.length; i++) {
        if(i == subVal) {
          continue;
        }
        sum += this.selectedActuals[i] == -1 ? 0 : this.selectedActuals[i];
      }
      return sum
    },
    optionUnavalible(buttonVal) {
      let sum = this.getSum(this.currentidx);

      if(sum + buttonVal > this.maxCards) {
        return true;
      }

      return false;
    },
    startNextRound() {
      this.$emit('start-next-round', this.newTotScores);
    }
  },
  computed: {
    canDoNextRound() {
      for(var i = 0; i < this.players.length; i++) {
        if(this.selectedActuals[i] == -1) {
          return false;
        }
      }
      let sum = this.getSum(-1);
      if(sum != this.maxCards) {
        return false;
      }

      return true;
    }
  }
}
</script>