<template>
    <v-container>
      <v-card
        class="mx-auto"
        outlined
        >
          <v-card-title class="justify-center">{{ arrangedPlayers[0] }} wins!</v-card-title> 
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Place
                  </th>
                  <th class="text-left">
                    Name
                  </th>
                  <th class="text-left">
                    Total Score
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(player, i) in arrangedPlayers"
                  :key="i"
                >
                  <td>{{ getMedal(i+1) }}{{ i+1 }}</td>
                  <td>
                    {{ player }}
                    <!--<v-chip
                      :color="getColor(bets[i],scores[i])"
                      v-if="bets[i] != -1"
                      dark
                    >
                      {{ scores[i] == -1 ? bets[i] : scores[i] }}
                    </v-chip>-->
                  </td>
                  <td>{{ arrangedScores[i] }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <div class="d-flex justify-center">
            <v-btn
            rounded
            color="blue"
            class="ma-2"
            @click="playAgain"
            dark
            >
              play again
            </v-btn>
          </div>
      </v-card>
    </v-container>
</template>

<script>
export default {
    name: 'GameOverScreen',
    props: {
        players: Array,
        scores: Array
    },
    data: () => ({
      arrangedPlayers: Array,
      arrangedScores: Array
    }),
    created() {
      this.organizeLeaderboard();
    },
    methods: {
      organizeLeaderboard() {
        var indices = Array.from(Array(this.scores.length).keys())
                  .sort((a, b) => this.scores[a] > this.scores[b] ? -1 : (this.scores[b] > this.scores[a]) | 0)
        console.log(indices);

        this.arrangedPlayers = indices.map(i => this.players[i]);
        this.arrangedScores = indices.map(i => this.scores[i]);
      },
      getMedal(place) {
        if(place == 1) {
          return '';
        }
        else if(place == 2) {
          return '';
        }
        else if(place == 3) {
          return '';
        }
      },
      playAgain() {
        this.$emit('play-again');
      }
    }
}
</script>