<template>
   <v-container>
      <v-card
        class="mx-auto"
        outlined
        >
          <v-card-title class="justify-center">Scores</v-card-title> 
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Name
                  </th>
                  <th class="text-left">
                    {{!isScoring ? "Round Bid" : "Round Score"}}
                  </th>
                  <th class="text-left">
                    Total Score
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(player, i) in players"
                  :key="i"
                >
                  <td>{{ player }}</td>
                  <td>
                    <v-chip
                      :color="getColor(bets[i],scores[i])"
                      v-if="bets[i] != -1"
                      dark
                    >
                      {{ scores[i] == -1 ? bets[i] : scores[i] }}
                    </v-chip>
                  </td>
                  <td>{{ totScores[i] }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <!-- <div 
            v-for="(player, i) in players"
            :key="i"
          >
            <v-card-text class="text-h6 py-2">{{player}}</v-card-text>
          </div> -->
      </v-card>
   </v-container> 
</template>

<script>
export default {
  name: 'RoundScores',
  props: {
    players: Array,
    bidder: String,
    bets: Array,
    scores: Array,
    totScores: Array,
    isScoring: Boolean
  },
  data: () => ({
    currentidx: 0
  }),
  methods: {
    getColor(bid, actual) {
      if(actual == -1) {
        return 'blue';
      }
      else if(bid + 10 == actual) {
        return 'green';
      }
      else {
        return 'red';
      }
    }
  }
}
</script>